import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from 'src/service/auth.service';
import { SpinnerService } from 'src/service/spinner.service';
import { User } from 'src/service/system.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less'],
})
export class LoginComponent implements OnInit {
  validateForm!: FormGroup;

  constructor(private fb: FormBuilder, private router: Router, private auth: AuthService, private spinner: SpinnerService) {}

  ngOnInit(): void {
    this.validateForm = this.fb.group({
      account: [null, [Validators.required]],
      password: [null, [Validators.required]],
      remember: [true],
    });
  }

  submitForm(value: { account: string; password: string; remember: boolean }): void {
    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }

    if (this.validateForm.valid) {
      this.spinner.show('spinner', 'login...');
      this.auth.login(value.account, value.password).then((u: User) => {
        setTimeout(() => {
          this.spinner.hidde('spinner');
          this.router.navigate([this.auth.redirectUrl]);
        }, 1000);
      });
    }
  }
}
